<template>
    <nav>
    <v-app-bar app fixed elevation="3">
        <div style="width: 270px">
          <v-btn plain @click="changeTheme">
            <v-icon>auto_fix_normal</v-icon>
            <em>{{magic}}</em>
          </v-btn>
        </div>
        <v-spacer></v-spacer>
        <div v-if="profileUsername">
          <v-btn to="/contact-us" plain class="ma-2">
            <v-icon left>contact_page</v-icon>CONTACTO
          </v-btn>
          <v-btn to="/shop" plain class="ma-2">
            <v-icon left>local_mall</v-icon>TIENDA
          </v-btn>
          <v-btn to="/home" plain class="ma-2">
            <v-icon left>feed</v-icon>NOSOTROS
          </v-btn>
        </div>
        <v-spacer></v-spacer>
        <div style="width: 270px">
          <v-btn v-if="profileUsername" to="/shop" plain>
            ESCARBATO PETSHOP
          </v-btn>
          <v-btn v-else plain>
            ESCARBATO PETSHOP
          </v-btn>
          <v-btn v-if="profileUsername" @click.stop="drawer = !drawer" plain>
            <v-icon>person</v-icon>
          </v-btn>
        </div>
      </v-app-bar>
      <v-navigation-drawer
        v-model="drawer"
        temporary
        right
        app
        >
          <v-list
            nav
            dense
          >
            <v-list-item-group
              active-class="text--accent-4"
            >
              <v-list-item to="/profile">
                <v-list-item-icon>
                  <v-icon left>account_circle</v-icon>
                </v-list-item-icon>
                <v-list-item-title>{{profileUsername}}</v-list-item-title>
              </v-list-item>
  
              <v-list-item v-if="role==='ADMIN'" to="/admin-products">
                <v-list-item-icon>
                  <v-icon left>admin_panel_settings</v-icon>
                </v-list-item-icon>
                <v-list-item-title>ADMINISTRAR PRODUCTOS</v-list-item-title>
              </v-list-item>

              <v-list-item to="/cart">
                <v-list-item-icon>
                  <v-icon left>shopping_cart</v-icon>
                </v-list-item-icon>
                <v-list-item-title>CARRITO</v-list-item-title>
              </v-list-item>
  
              <v-list-item to="/history-shop">
                <v-list-item-icon>
                  <v-icon left>history</v-icon>
                </v-list-item-icon>
                <v-list-item-title>HISTORIAL DE COMPRAS</v-list-item-title>
              </v-list-item>

              <v-list-item @click="closeSesion">
                <v-list-item-icon>
                  <v-icon left>logout</v-icon>
                </v-list-item-icon>
                <v-list-item-title>CERRAR SESIÓN</v-list-item-title>
              </v-list-item>
            </v-list-item-group>
          </v-list>
      </v-navigation-drawer>
    </nav>
  </template>
  
  <script>
import { mapMutations } from 'vuex'
  export default {
    data(){
      return{
        drawer: false,
        group: null,
      }
    },
    methods:{
      ...mapMutations(['setUser']),
      changeTheme(){
        this.$vuetify.theme.dark = !this.$vuetify.theme.dark
      },
      closeSesion(){
        this.setUser({
          token: null,
          username: null,
          name: null,
          surname: null,
          email: null
        })
        this.$router.push('/login')
      }
    },
    computed:{
      magic(){
        if(this.$vuetify.theme.dark) return 'Lumos'
        else return 'Nox'
      },
      profileUsername(){
        if(this.$store.state.userModule.user.username && this.$store.state.userModule.user.username.length>0) return this.$store.state.userModule.user.username.toUpperCase()
        else return false
      },
      role(){
        return this.$store.state.userModule.user.role
      }
    }
  }
  </script>
  
  <style scoped>
  </style>