<template>
  <app-navbar-desktop v-if = "desktop"></app-navbar-desktop>
  <app-navbar-phone v-else></app-navbar-phone>
</template>

<script>
  import AppNavbarDesktop from './AppNavbarDesktop.vue'
  import AppNavbarPhone from './AppNavbarPhone.vue'

  export default {
  components: { AppNavbarDesktop, AppNavbarPhone },
      computed:{
          desktop(){
              switch(this.$vuetify.breakpoint.name){
              case 'xs': return false
              case 'sm': return false
              case 'md': return true
              case 'lg': return true
              case 'xl': return true
              }
              return false
          }
      }
  }
</script>
  
