import gql from 'graphql-tag'
import apolloClient from '../apollo'
import axios from 'axios'

class ProductProvider {
    constructor(){
        this.gqlc = null
    }

    setGqlc(gqlc){
        this.gqlc = gqlc
    }

    async fetchProducts(){
        return await this.gqlc.query({
            //authUser(username:String!, password:String!):UserLogin
            query: gql`
                query{
                    getProducts{
                        image
                        title
                        description
                        price
                        category
                    }
                } 
            `
        })
    }

    async updateProducts(token, image, title, description, price, category){
        return await this.gqlc.mutate({
            mutation: gql`
                mutation{
                    updateProduct(product:{
                        token: ${JSON.stringify(token)}
                        image: ${JSON.stringify(image)}
                        title: ${JSON.stringify(title)}
                        description: ${JSON.stringify(description)}
                        price: ${JSON.stringify(price)}
                        category: ${JSON.stringify(category)}
                    }){
                        image
                        title
                        description
                        price
                        category
                    }
                }
            `
        })
    }

    async deleteProduct(token, title){
        return await this.gqlc.mutate({
            mutation: gql`
                mutation{
                    deleteProduct(token: ${JSON.stringify(token)}, title: ${JSON.stringify(title)}){
                        image
                        title
                        description
                        price
                        category
                    }
                }
            `
        })
    }

    async createProduct(token, image, title, description, price, category){
        return await this.gqlc.mutate({
            mutation: gql`
                mutation{
                    createProduct(product:{
                        token: ${JSON.stringify(token)}
                        image: ${JSON.stringify(image)}
                        title: ${JSON.stringify(title)}
                        description: ${JSON.stringify(description)}
                        price: ${JSON.stringify(price)}
                        category: ${JSON.stringify(category)}
                    }){
                        image
                        title
                        description
                        price
                        category
                    }
                }
            `
        })
    }

    async fetchProductsFilter(category, title, page, items){
        return await this.gqlc.query({
            query: gql`
                query{
                    getFilterProducts(filter:{
                        category: ${JSON.stringify(category)}
                        title: ${JSON.stringify(title)}
                        page: ${page}
                        items: ${items}
                    }){
                        image
                        title
                        description
                        price
                        category
                    }
                }
            `
        })
    }

    async uploadImage(image){
        if(!image) return null            
        const file = new FormData()
        file.append('file', image)
        return await axios.post('/api/upload', file, {
            header: {
                'Content-type' : 'multipart/form-data'
            }
        })
    }

    async fetchCategories(){
        return await this.gqlc.query({
            query: gql`
                query{
                    getCategories
                }
            `
        })
    }
}

const productProvider = new ProductProvider()
productProvider.setGqlc(apolloClient)
export default productProvider