  <script>
import { mapMutations } from 'vuex'
  export default {
    data(){
      return{
        drawerMenu: false,
        drawerProfile: false,
        group: null,
      }
    },
    methods:{
      ...mapMutations(['setUser']),
      changeTheme(){
        this.$vuetify.theme.dark = !this.$vuetify.theme.dark
      },
      closeSesion(){
        this.setUser({
          token: null,
          username: null,
          name: null,
          surname: null,
          email: null
        })
        this.$router.push('/login')
      }
    },
    computed:{
      magic(){
        if(this.$vuetify.theme.dark) return 'Lumos'
        else return 'Nox'
      },
      profileUsername(){
        return this.$store.state.userModule.user.username
      },
      role(){
        return this.$store.state.userModule.user.role
      }
    }
  }
