<template>
  <v-row>
    <v-col col="12" sm="12" offset-md="1" order-md="1" md="10" offset-lg="2" order-lg="2" lg="8" offset-xl="3" order-xl="3" xl="6">
      <v-card class="ma-auto mt-16 pa-10">
        <v-card-title class="text-h5 justify-center">Iniciar Sesion</v-card-title>
        <v-divider></v-divider>
        <v-form v-model="formValue">
          <v-text-field
            label="username"
            v-model="username"
            :rules="inputRules"
          ></v-text-field>
          <v-text-field
            type="password"
            label="password"
            v-model="password"
            :rules="inputRules"
            v-on:keyup.enter="login"
          ></v-text-field>
          <v-card-actions>
            <v-btn v-if="!formValue" block color="primary" disabled :loading="loginLoad" @click="login">Ingresar</v-btn>
            <v-btn v-else block color="primary" :loading="loginLoad" @click="login">Ingresar</v-btn>
          </v-card-actions>
          <v-divider></v-divider>
          <v-card-text class="text-h6 text-center">¿Tienes problemas para acceder a tu cuenta?</v-card-text>
          <v-card-actions>
            <v-btn block plain color="green">necesito ayuda</v-btn>
          </v-card-actions>
          <v-divider></v-divider>
          <v-card-text class="text-h6 text-center">¿No tienes cuenta?</v-card-text>
          <v-card-actions>
            <v-btn to="/register" block color="secondary">Registrarme</v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import userProvider from '@/providers/UserProvider';
import { mapMutations } from 'vuex';

export default {
  data(){
    return{
      loginLoad: false,
      formValue: false,
      username: '',
      password: '',
      inputRules: [value => !!value || 'Este campo no debe estar vacío.']
    }
  },
  methods:{
    ...mapMutations(['setUser', 'setAlert']),
    async login(){
      if(this.formValue){
        this.loginLoad = true
        try {
          const {data: {authUser: user}} = await userProvider.loginUser(this.username, this.password)
          this.loginLoad = false
          this.setAlert({
            state: true,
            text: 'Inicio de sesión con exito.',
            type: 'success'
          })
          this.setUser(user)
          this.$router.push('/home')
        } catch (error) {
          this.setAlert({
            state: true,
            text: 'Credenciales invalidas.',
            type: 'error'
          })
          this.loginLoad = false
        }
      }
    }
  }
}
</script>

<style scoped>
  .container{
    height: 100vh;
  }
</style>