const alertModule = {
    state: {
        alert: {
            state: false,
            text: '',
            type: 'success'
        }
    },
    mutations: {
        setAlert(moduleState, {state, text, type}){
            moduleState.alert = {state, text, type}
            setTimeout(() => {
                moduleState.alert = {
                    state: false,
                    text: '',
                    type: 'success'
                }
            }, 3000);
        }
    },
    modules: {
    }
}

export default alertModule