<template>
    <div>   
        <v-row class="my-10 mx-4">
          <v-col col="12" sm="12">
              <v-card class="pa-4" v-if="display == 'lg' || display == 'xl'">
                  <v-row>
                      <v-col cols="12" sm="1">
                          <v-card-text class="text-center">IMAGEN</v-card-text>
                      </v-col>
                      <v-col cols="12" sm="2">
                          <v-card-text class="text-center">NOMBRE DEL PRODUCTO</v-card-text>
                      </v-col>
                      <v-col cols="12" sm="2">
                          <v-card-text class="text-center">PRECIO DEL PRODUCTO</v-card-text>
                      </v-col>
                      <v-col cols="12" sm="2">
                          <v-card-text class="text-center">DESCRIPCIÓN</v-card-text>
                      </v-col>
                      <v-col cols="12" sm="2">
                          <v-card-text class="text-center">CATEGORÍA</v-card-text>
                      </v-col>
                      <v-col cols="12" sm="2">
                          <v-card-text class="text-center">UNIDADES</v-card-text>
                      </v-col>
                      <v-col cols="12" sm="1">
                          <v-card-text class="text-center">ACCIONES</v-card-text>
                      </v-col>
                  </v-row>
              </v-card>
              <v-card class="pa-4" v-else-if="cart.length>0 && (display == 'xs' || display =='sm' || display == 'md')">
                  <v-row class="align-center">
                      <v-col col="4" sm="4" md="4">
                          <v-card-text class="text-center">TOTAL</v-card-text>
                      </v-col>
                      <v-col col="4" sm="4" md="4">
                          <v-card-text class="text-center green--text">${{total}}</v-card-text>
                      </v-col>
                      <v-col col="4" sm="4" md="4">
                          <v-row class="justify-center align-center">
                              <v-btn fab color="success" @click="buy" v-if="display =='lg' || display == 'xl'">
                                  <v-icon>paid</v-icon>
                              </v-btn>
                              <v-btn block color="success" @click="buy" v-else>
                                  COMPRAR
                              </v-btn>
                          </v-row>
                      </v-col>
                  </v-row>
              </v-card>
          </v-col>
          <v-col cols="12" sm="12" v-if="cart.length>0">
              <v-card class="pa-4 my-4" v-for="(product, index) of cart" v-bind:key="product.title">
                  <v-row class="align-center" >
                      <v-col cols="12" sm="12" md="12" lg="1">
                          <v-img :src="product.image" aspect-ratio="1"></v-img>
                      </v-col>
                      <v-col cols="6" sm="6" md="6" lg="2">
                          <v-card-text class="text-center">{{product.title}}</v-card-text>
                      </v-col>
                      <v-col cols="6" sm="6" md="6" lg="2">
                          <v-card-text class="text-center green--text">${{product.price}}</v-card-text>
                      </v-col>
                      <v-col cols="12" sm="12" md="12" lg="2">
                          <v-card-text class="text-center">{{product.description}}</v-card-text>
                      </v-col>
                      <v-col cols="6" sm="6" md="6" lg="2" v-if="display == 'lg' || display == 'xl'">
                          <v-card-text class="text-center">{{product.category}}</v-card-text>
                      </v-col>
                      <v-col cols="6" sm="6" md="6" lg="2">
                          <v-text-field class="mx-auto" type="number" min="1" style="width: 100px" v-model="product.units"></v-text-field>
                      </v-col>
                      <v-col cols="6" sm="6" md="6" lg="1">
                          <v-row class="justify-center align-center">
                              <v-btn fab color="red" @click="deletePetition(index)">
                                  <v-icon>delete</v-icon>
                              </v-btn>
                          </v-row>
                      </v-col>
                  </v-row>
              </v-card>
          </v-col>
        </v-row>
        <v-row v-if="display =='lg' || display == 'xl'" class="mx-4">
            <v-col col="12" sm="12">
              <v-card class="pa-4" v-if="cart.length>0">
                  <v-row class="align-center">
                      <v-col col="12" sm="12" md="12" lg="2">
                          <v-card-text class="text-center">TOTAL</v-card-text>
                      </v-col>
                      <v-col col="12" sm="12" md="12" lg="1" offset-lg="2">
                          <v-card-text class="text-center green--text">${{total}}</v-card-text>
                      </v-col>
                      <v-col col="12" offset-sm="6" sm="1">
                          <v-row class="justify-center align-center">
                              <v-btn fab color="success" @click="buy" v-if="display =='lg' || display == 'xl'">
                                  <v-icon>paid</v-icon>
                              </v-btn>
                              <v-btn block color="success" @click="buy" v-else>
                                  COMPRAR
                              </v-btn>
                          </v-row>
                      </v-col>
                  </v-row>
              </v-card>
          </v-col>
        </v-row>
        <v-dialog v-model="dialog" width="50%">
            <v-card>
        <v-card-title class="justify-center">
          ¿Está seguro que quiere eliminar el producto de su carrito?
        </v-card-title>

        <v-divider></v-divider>

        <v-card-actions>
          <v-btn
            color="primary"
            @click="dialog = !dialog"
            width="40%"
          >
            NO
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            color="error"
            @click="deleteProduct"
            width="40%"
            :loading="deleteLoading"
          >
            SI
          </v-btn>
        </v-card-actions>
      </v-card>
        </v-dialog>
    </div> 
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    name: 'CartView',
    data(){
        return{
            deleteLoading: false,
            index: null,
            cart: [],
            dialog: false,
        }
    },
    created(){
        this.cart = this.$store.state.cartModule.cart
    },
    computed:{
        total(){
            let total = 0
            this.cart.forEach(e=>{
                total += Number(e.price) * e.units
            })
            return total
        },
        display(){
            return this.$vuetify.breakpoint.name
        }
    },
    methods:{
        ...mapMutations(['deleteShop']),
        buy(){
            let msg = 'Hola! quería comprar los siguientes productos: %0A'
            this.cart.forEach( e => {
                msg += '- ' + e.units + ' unidades de ' + e.title + '%0A'
            })
            window.open(`whatsapp://send?text=${msg}&phone=+5491153326667`)
        },
        deletePetition(index){
            this.index = index
            this.dialog = true
        },
        deleteProduct(){
            this.deleteLoading = true
            this.deleteShop(this.product)
            this.deleteLoading = false
            this.dialog = false
        }
    }
}
</script>

