import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginView from '../views/LoginView.vue'
import RegisterView from '../views/RegisterView.vue'
import AboutView from '../views/AboutView.vue'
import ProfileView from '../views/ProfileView.vue'
import HistoryShop from '../views/HistoryShopView.vue'
import Cart from '../views/CartView.vue'
import AdminProducts from '../views/AdminProductView.vue'
import Shop from '../views/ShopView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    name: 'all',
    component: HomeView
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/contact-us',
    name: 'about',
    component: AboutView
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/register',
    name: 'register',
    component: RegisterView
  },
  {
    path: '/profile',
    name: 'profile',
    component: ProfileView
  },
  {
    path: '/history-shop',
    name: 'historyShop',
    component: HistoryShop
  },
  {
    path: '/cart',
    name: 'cart',
    component: Cart
  },
  {
    path: '/admin-products',
    name: 'adminProducts',
    component: AdminProducts
  },
  {
    path: '/shop',
    name: 'shop',
    component: Shop
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
