import gql from 'graphql-tag'
import apolloClient from '../apollo'

class UserProvider{
    constructor(){
        this.gqlc = null
    }

    setGqlc(gqlc){
        this.gqlc = gqlc
    }

    async createUser(username, password, email){
        return await this.gqlc.mutate({
            mutation: gql`
                mutation{
                    createUser(user: {
                        username: ${JSON.stringify(username)}
                        password: ${JSON.stringify(password)}
                        email: ${JSON.stringify(email)}
                    }){
                        username
                        name
                        surname
                        address
                        email
                        role
                    }
                }
            `
        })
    }

    async loginUser(username, password){
        return await this.gqlc.query({
            //authUser(username:String!, password:String!):UserLogin
            query: gql`
                query{
                    authUser(username:${JSON.stringify(username)}, password:${JSON.stringify(password)}){
                        username
                        name
                        surname
                        token
                        email
                        address
                        role
                    }
                } 
            `
        })
    }

    async authUser(token){
        return await this.gqlc.query({
            query: gql`
                query{
                    authUserByToken(token:${JSON.stringify(token)}){
                        username
                        name
                        surname
                        token
                        email
                        address
                        role
                    }
                }
            `
        })
    }

    async existUser(email='', username=''){
        return await this.gqlc.query({
            query: gql`
                query{
                    existUser(username:${JSON.stringify(username)}, email:${JSON.stringify(email)}){
                        email,
                        username
                    }
                }
            `
        })
    }

    async updateUser(token, username, password, name, surname, address, email){
        return await this.gqlc.mutate({
            mutation: gql`
                mutation{
                    updateUser(user: {
                        token: ${JSON.stringify(token)}
                        username: ${JSON.stringify(username)}
                        password: ${JSON.stringify(password)}
                        name: ${JSON.stringify(name)}
                        surname: ${JSON.stringify(surname)}
                        address: ${JSON.stringify(address)}
                        email: ${JSON.stringify(email)}
                    }){
                        username
                        name
                        surname
                        address
                        email
                        role
                    }
                }
            `
        })
    }
}

const userProvider = new UserProvider()
userProvider.setGqlc(apolloClient)
export default userProvider