<template>
  <v-app>
    <app-navbar/>
    <v-main>
      <router-view></router-view>
    </v-main>
    <alert-snackbar v-if="alert.state" :text="alert.text" :type="alert.type"/>
    <v-overlay :value="loading">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
  </v-app>
</template>

<script>
import { mapMutations } from 'vuex'
import AlertSnackbar from './components/AlertSnackbar.vue'
import AppNavbar from './components/AppNavbar.vue'
import userProvider from './providers/UserProvider'

export default {
  components: { AppNavbar, AlertSnackbar },
  name: 'app',
  computed:{
    loading(){
      return this.$store.state.loading
    },
    alert(){
      return this.$store.state.alertModule.alert
    },
    currentRoute(){
      return this.$route.name
    }
  },
  watch:{
    currentRoute(){
      this.setLoading(true)
      setTimeout(() => {
        this.setLoading(false)
      }, 1000);
    }
  },
  methods:{
    ...mapMutations(['setAlert', 'setUser', 'setLoading'])
  },
  async created(){

    this.setLoading(true)
    setTimeout(() => {
      this.setLoading(false)
    }, 1000);

    const {token} = this.$store.state.userModule.user
    if(!token){
      this.setAlert({
        state: true,
        text: 'Para ingresar a esta sección primero debe loguearse',
        type: 'warning'
      })
      this.$router.push('/login')
      return
    }
    try {
      const {data: {authUserByToken}} = await userProvider.authUser(token)
      this.setUser(authUserByToken)
      switch(this.$route.name){
        case 'login': 
          this.$router.push('/home')
          break
        case 'register': 
          this.$router.push('/home')
          break
      }
    } catch (error) {
      this.setAlert({
        state: true,
        text: 'Sesión expirtada. Ingresar denuevo.',
        type: 'error'
      })
      this.$router.push('/login')
    }
  }
}

</script>

