<template>
  <v-row class="my-10">
    <v-col col="12" sm="12" md="12" offset-lg="1" lg="10" order="1" offset-xl="2" xl="8" order-xl="2">
        <v-card
        class="overflow-hidden"
        >
            <v-toolbar
            flat
            dark
            >
                <v-icon>mdi-account</v-icon>
                <v-toolbar-title>User Profile</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn
                color="blue darken-3"
                fab
                small
                @click="isEditing = !isEditing"
            >
                <v-icon v-if="!isEditing">
                mdi-close
                </v-icon>
                <v-icon v-else>
                mdi-pencil
                </v-icon>
            </v-btn>
            </v-toolbar>
            <v-card-text>
                <v-form v-model="formValue">
                    <v-text-field
                        autocomplete="off"
                        disabled
                        label="username"
                        :value="username"
                    ></v-text-field>
                    <v-text-field
                        :disabled="isEditing"
                        label="correo electronico"
                        v-model="email"
                    ></v-text-field>
                    <v-text-field
                        :disabled="isEditing"
                        label="nombre"
                        v-model="name"
                        :rules="inputRules"
                    ></v-text-field>
                    <v-text-field
                        :disabled="isEditing"
                        label="apellido"
                        v-model="surname"
                        :rules="inputRules"
                    ></v-text-field>
                    <v-text-field
                        :disabled="isEditing"
                        label="dirección"
                        v-model="address"
                        :rules="inputRules"
                    ></v-text-field>
                    <v-divider></v-divider>
                    <v-card-title>Actualizar contraseña</v-card-title>
                    <v-text-field
                        :autocomplete="password"
                        type="password"
                        :disabled="isEditing"
                        label="nueva contraseña"
                        v-model="password"
                    ></v-text-field>
                    <v-text-field
                        type="password"
                        :disabled="isEditing"
                        label="repita su contraseña"
                        :rules="passwordRules"
                    ></v-text-field>
                </v-form>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
                :loading="saveLoading"
                color="success"
                @click="upload"
                block
            >
                Guardar Cambios
            </v-btn>
            </v-card-actions>
        </v-card>
    </v-col>
  </v-row>
</template>

<script>
  import validator from 'validator'
import userProvider from '../providers/UserProvider'
import { mapMutations } from 'vuex'

export default {
    name: 'ProfileView',
    data(){
        return{
            formValue: false,
            saveLoading: false,
            isEditing: false,
            username: '',
            email: '',
            name: '',
            surname: '',
            address: '',
            password: '',
            inputRules: [
                value => !!value || 'Este campo no debe permanecer vacío.'
            ]
        }
    },
    computed:{
        usernameStore(){
            return this.$store.state.userModule.user.username
        },
        emailStore(){
            return this.$store.state.userModule.user.email
        },
        nameStore(){
            return this.$store.state.userModule.user.name
        },
        surnameStore(){
            return this.$store.state.userModule.user.surname
        },
        addressStore(){
            return this.$store.state.userModule.user.address
        },
        passwordRules(){
          return [
            value => (this.password == value || this.password.length == 0) || 'Las contraseñas deben ser iguales.'
          ]
        },
        emailRules(){
          return [
            value => !!value || 'Este campo no puede estar vacío.',
            value => validator.isEmail(value) || 'Debe contener un email valido.',
            () => !this.existEmail || 'Este email ya está en uso.'
          ]
        },
        usernameRules(){
          return [
            () => !this.existUsername || 'Este username ya está en uso.'
          ]
        }
    },
    created(){
        this.username = this.usernameStore
        this.email = this.emailStore
        this.name = this.nameStore
        this.surname = this.surnameStore
        this.address = this.addressStore
        this.password = ''
    },
    methods:{
        ...mapMutations(['setAlert', 'setUser']),
        async upload(){
            if(!this.formValue){
                this.setAlert({
                    state: true,
                    text: 'Algunos de los campos están mal.',
                    type: 'error'
                })
                return
            }
            this.saveLoading = true
            try {
                await userProvider.updateUser(this.$store.state.userModule.user.token, this.username, this.password, this.name, this.surname, this.address, this.email)
                this.setAlert({
                    state: true,
                    text: 'Cambios guardados correctamente',
                    type: 'success'
                })
                //SOFT UPDATE
                this.setUser({
                    token: this.$store.state.userModule.user.token,
                    username: this.$store.state.userModule.user.username,
                    name: this.name,
                    surname: this.surname,
                    email: this.email,
                    address: this.address
                })
                this.saveLoading = false
            } catch (error) {
                this.setAlert({
                    state: true,
                    text: 'Ocurrió un problema al guardar los cambios intente denuevo.',
                    type: 'error'
                })
                this.saveLoading = false
            }
        }
    }
}
</script>

