const cartModule = {
    state: {
        cart: []
    },
    mutations: {
        addShop(state, {image, title, price, description, category}){
            state.cart.push({
                image,
                title,
                price,
                description,
                category,
                units: 1
            })
        },
        deleteShop(state, index){
            state.cart.splice(index, 1)
        }
    }
}

export default cartModule