<template>
    <v-container class="container">
      <v-row>
        <v-col col="12" sm="12" offset-md="1" order-md="1" md="10" offset-lg="2" order-lg="2" lg="8" offset-xl="3" order-xl="3" xl="6">
          <v-card class="ma-auto mt-16 pa-10">
            <v-card-title class="text-h5 justify-center">Registrarme</v-card-title>
            <v-divider></v-divider>
            <v-form v-model="formValue">
              <v-text-field
                label="email"
                v-model="email"
                :rules="emailRules"
                @keydown="validateEmail"
              ></v-text-field>
              <v-text-field
                label="username"
                v-model="username"
                :rules="usernameRules"
                @keydown="validateUsername"
              ></v-text-field>
              <v-text-field
                label="contraseña"
                v-model="password"
                :rules="inputRules"
                type="password"
              ></v-text-field>
              <v-text-field
                label="contraseña"
                v-model="passwordCompare"
                :rules="passwordRules"
                type="password"
                v-on:keyup.enter="register"
              ></v-text-field>
              <v-card-actions>
                <v-btn v-if="formValue" block color="primary" :loading="registerLoad" @click="register">Registrarme</v-btn>
                <v-btn v-else disabled block color="primary" :loading="registerLoad" @click="register">Registrarme</v-btn>
              </v-card-actions>
              <v-divider></v-divider>
              <v-card-text class="text-h6 text-center">¿Ya tienes cuenta?</v-card-text>
              <v-card-actions>
                <v-btn to="/login" block color="secondary">Iniciar Sesión</v-btn>
              </v-card-actions>
            </v-form>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </template>
  
  <script>
  import validator from 'validator'
  import userProvider from '../providers/UserProvider'
  import { mapMutations } from 'vuex'

  export default {
    data(){
      return{
        existEmail: false,
        existUsername: false,
        registerLoad: false,
        formValue: false,
        email: '',
        username: '',
        password: '',
        passwordCompare: '',
        inputRules: [
          value => !!value || 'Este campo no debe permanecer vacío.'
        ]
      }
    },
    computed:{
        passwordRules(){
          return [
            value => this.password === value || 'Las contraseñas deben ser iguales.'
          ]
        },
        emailRules(){
          return [
            value => !!value || 'Este campo no puede estar vacío.',
            value => validator.isEmail(value) || 'Debe contener un email valido.',
            () => !this.existEmail || 'Este email ya está en uso.'
          ]
        },
        usernameRules(){
          return [
            value => !!value || 'Este campo no puede estar vacío.',
            () => !this.existUsername || 'Este username ya está en uso.'
          ]
        }
          
    },
    methods:{
      ...mapMutations(['setAlert']),
      async register(){
        this.validate()
        if(this.formValue){
          this.registerLoad = true
          try{
            await userProvider.createUser(this.username, this.password, this.email)
            this.registerLoad = false
            this.$router.push('/login')
            this.setAlert({
              state: true,
              type: 'success',
              text: 'Usuario Registrado con exito. Inicia sesión para acceder!'
            })
          }catch(error){
            this.setAlert({
              state: true,
              type: 'error',
              text: 'Ocurrió un error al registrarse. Prueba nuevamente.'
            })
            this.registerLoad = false
          }
        }
      },
      async validate(){
        const {data: {existUser: {email, username}}} = await userProvider.existUser(this.email, this.username)
        this.existEmail = email
        this.existUsername = username
      },
      async validateEmail(event){
        if(event.code.includes('Key')){
          const {data: {existUser: {email}}} = await userProvider.existUser(this.email + event.key)
          this.existEmail = email
        }else{
          const {data: {existUser: {email}}} = await userProvider.existUser(this.email)
          this.existEmail = email
        }
      },
      async validateUsername(event){
        const {data: {existUser: {username}}} = await userProvider.existUser('', this.username + event.key)
        this.existUsername = username
      }
    }
  }
  </script>
  
  <style scoped>
    .container{
      height: 100vh;
    }
  </style>