<template>
  <div class="my-10">
    <v-card width="80vw" class="mx-auto pa-4">
        <v-row class="justify-center align-center">
            <v-col cols="12" sm="12" md="6">
                <v-combobox :items="categories" label="categoría" v-model="category" @change="fetchProducts(true)"></v-combobox>
            </v-col>
            <v-col cols="12" sm="12" md="6">
                <v-text-field label="buscar" v-model="search"></v-text-field>
            </v-col>
            <v-col cols="12">
                <v-btn block color="primary" @click="fetchProducts(true)">BUSCAR</v-btn>
            </v-col>
        </v-row>
        <v-divider></v-divider>
        <v-row class="justify-center align-center">
            <v-col cols="12" sm="12" md="12" lg="4" xl="4" v-for="product of products" v-bind:key="product.title">
                <v-card class="pa-4">
                    <v-app-bar-title class="justify-center">{{product.title}}</v-app-bar-title>
                    <v-img aspect-ratio="1" :src="product.image"></v-img>
                    <v-row class="justify-center align-center">
                        <v-col cols="12" sm="12" md="12" lg="8" xl="8">
                            <v-card-text class="text-center">{{product.description}}</v-card-text>
                        </v-col>
                        <v-col cols="12" sm="12" md="12" lg="4" xl="4">
                            <v-card-text class="text-center success--text text-h5">${{product.price}}</v-card-text>
                        </v-col>
                    </v-row>
                    <v-divider></v-divider>
                    <v-row>
                        <v-col cols="12" sm="12" md="12" lg="6" xl="6">
                            <v-btn
                            color="primary"
                            class="ma-2 white--text"
                            @click="addToCart(product)"
                            block
                            >
                            Añadir al carrito
                            <v-icon
                                right
                                dark
                            >
                            add_shopping_cart
                            </v-icon>
                            </v-btn>
                            <v-spacer></v-spacer>
                        </v-col>
                        <v-col cols="12" sm="12" md="12" lg="6" xl="6">
                            <v-btn
                            color="success"
                            class="ma-2 white--text"
                            @click="consultWhatsapp(product.title, product.price)"
                            block
                            >
                            whatsapp
                            <v-icon
                                right
                                dark
                            >
                            monetization_on
                            </v-icon>
                            </v-btn>
                        </v-col>
                    </v-row>
                        <v-row>
                            <v-col cols="6">
                            </v-col>
                            <v-col  cols="6">
                            </v-col>
                        </v-row>
                </v-card>
            </v-col>
        </v-row>
        <v-row>
            <v-btn v-if="page>1" plain @click="changePage(page-1)">{{'< página anterior'}}</v-btn>
            <v-spacer></v-spacer>            
            <v-btn v-if="existProxPage" plain @click="changePage(page+1)">{{'próxima página >'}}</v-btn>
        </v-row>
    </v-card>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import productProvider from '../providers/ProductProvider'
export default {
    data(){
        return{
            categories: [],
            products: [],
            search: '',
            category: 'TODOS',
            page: 1,
            existProxPage: false,
        }
    },
    async created(){
        this.setLoading(true)
        const {data: {getCategories}} = await productProvider.fetchCategories()

        this.categories = getCategories

        this.fetchProducts()
        this.setLoading(false)
    },
    mounted(){
        this.fetchProducts()
    },
    watch:{
        products(value){
            if(value.length == 0){
                this.category = 'TODOS'
                this.search = ''
                this.setAlert({
                    state: true,
                    text: 'No se encontraron productos',
                    type: 'error'
                })
                this.fetchProducts()
            }
        }
        },
    methods:{
        ...mapMutations(['setAlert', 'addShop', 'setLoading']),
        addToCart(product){
            this.addShop(product)
            this.setAlert({
                state:true,
                text: 'Se ha añadido un nuevo producto a tu carrito',
                type: 'success'
            })
        },
        consultWhatsapp(title, price){
            window.open(`whatsapp://send?text=Buenas!! Quería hacerte consultar por el producto ${title} con un valor de $${price}...&phone=+5491153326667`)
        },
        changePage(page){
            this.page = page
            this.fetchProducts()
        },
        async fetchProducts(search){
            this.setLoading(true)
            if(search) this.page = 1
            try{
                if(this.$vuetify.breakpoint.name == "xs" || this.$vuetify.breakpoint.name == "sm" || this.$vuetify.breakpoint.name == "md"){
                    const {data: {getFilterProducts: products}} = await productProvider.fetchProductsFilter(this.category, this.search, 0, 0)
                    this.products = products
                }
                else{
                    const {data: {getFilterProducts: products}} = await productProvider.fetchProductsFilter(this.category, this.search, this.page, 3)
                    this.products = products
                    const {data: {getFilterProducts: proxProducts}} = await productProvider.fetchProductsFilter(this.category, this.search, this.page + 1, 3)
                    if(proxProducts.length==0) this.existProxPage = false
                    else this.existProxPage = true
                }
            }catch(error){
                this.setAlert({
                    state: true,
                    text: 'Ocurrió un problema al cargar los productos.',
                    type: 'error'
                })
            }   
            this.setLoading(false)
        }
    }
}
</script>

